.page.catalog .catalog-page__header{position:relative;display:block;padding:5.6rem 0 2.8rem;border-bottom:1px solid var(--divider-color);margin-bottom:4.6rem}.page.catalog .catalog-page__header h1{font-size:4.6rem;font-weight:600;width:max-content;padding:.8rem 1.2rem;border:1px solid var(--divider-color);box-shadow:8px -8px 0px 0px var(--primary-3)}.page.catalog .catalog-page__content{display:flex;flex-direction:column;align-items:stretch;justify-content:flex-start}@media screen and (min-width: 769px){.page.catalog .catalog-page__content{flex-direction:row;align-items:flex-start;justify-content:flex-start}}